<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-xs-2 protrait">
        <img class="img-circle head" :src="portraitImg"/>
      </div>
      <div class="col-xs-10">
        <p class="theme">{{ from }}</p>
      </div>
      <!--<div class="clearfix"></div>-->
    </div>
    <div>
      <router-link to="/question/11123" class="title">{{ title }}</router-link>
    </div>
    <div>
      <img class="abImg" :src="abstractImg"/>
    </div>
    <div>
      <router-link to="/answer/13321">{{ author }}{{ abstract }}</router-link>
    </div>
    <div class="row tail">
      <div class="col-xs-3">
        <p>{{ likeCount }} </p>
      </div>
      <div class="col-xs-3">
        <router-link to="/comment/13431">{{ commentCount }} </router-link>
      </div>
      <div class="col-xs-6">
        <span @click="follow">{{ attention }}</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'questionItem',
    data () {
      return {
        from: '',
        title: '',
        author: 'bfjei',
        abstract: 'KPIABAB...',
        portraitImg: '/static/head.png',
        likeCount: '1122',
        commentCount: '21',
        attention: '',
        abstractImg: '/static/head.png',
        commentId: 123431
//        questionId: 13443
      }
    },
    methods: {
      follow () {
        this.attention = ''
      }
    }
  }
</script>

<style scoped>
  .container-fluid {
    padding-top: 2%;
    padding-bottom: 2%;
    margin-top: 2%;
  }
  .title {
    font-size: 18px;
    font-weight: bold;
    line-height: 30px;
    color: #000;
  }
  .theme {
    line-height: 40px;
  }
  p {
    margin: 0;
  }
  .abImg {
    width: 100%;
    height: 150px;
  }
  .tail {
    color: #aaaaaa;
  }
</style>



