<template>
  <div class="container-fluid">
    <div class="topicContainer">
      <span class="topic" v-for="word in words">{{word}}</span>
    </div>
    <div class="title">
      <p>{{ questionTitle }}</p>
    </div>
    <div class="desc">
      <p>{{ questionDesc }}</p>
    </div>
    <div class="row tail">
      <div class="col-xs-3">
        <span>{{ followCount }} </span>
      </div>
      <div class="col-xs-3">
        <span>{{ commentCount }} </span>
      </div>
      <div class="col-xs-6">
        <button class="btn"> </button>
      </div>
    </div>
    <div class="row answerDiv">
      <div class="col-xs-6 answer" style="border-right: solid; border-width: 1px; border-color: #dddddd;">
        <span class="answerBtn"></span>
      </div>
      <div class="col-xs-6 answer">
        <span class="answerBtn"></span>
      </div>
    </div>
    <div class="row countDiv">
      <div class="col-xs-6 answerCount">
        <span>{{ answerCount }}</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    components: {
    },
    name: 'questionDesc',
    data () {
      return {
        words: ['', '', '', '', '...'],
        questionTitle: '',
        questionDesc: 'iOSAPP',
        followCount: 43215,
        commentCount: 2343,
        answerCount: 31
      }
    }
  }
</script>

<style scoped>
  .title {
    font-weight: bold;
    font-size: 18px;
  }
  .tail {
    line-height: 40px;
    white-space: nowrap;
  }
  .topic {
    margin-right: 10px;
    padding: 5px;
    background-color: #EAF8FF;
    color: #698ebf;
  }
  .topicContainer {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .btn {
    float: right;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #0e7bef;
    color: #fff;
  }
  .answer {
    padding: 10px;
    text-align: center;
  }
  .answerDiv {
    border-top: solid;
    border-width: 1px;
    border-color: #dddddd;
  }
  .answerCount {
    text-align: left;
  }
  .countDiv {
    padding-top: 10px;
    background-color: #eeeeee;
    /*padding-bottom: 10px;*/
  }
</style>



