<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-xs-2 protrait">
        <img class="img-circle head" :src="portraitImg"/>
      </div>
      <div class="col-xs-10 right">
        <div>
          <p>{{ uname }}</p>
        </div>
        <div>
          <p>{{ commentContent }}</p>
        </div>
        <div class="row">
          <div class="col-xs-3">
            <span>{{ commentLikeCount }} </span>
          </div>
          <div class="col-xs-3">
            <span></span>
          </div>
          <div class="col-xs-6">
            <span>{{ commentTime }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'commentItem',
    data () {
      return {
        uname: 'Swim',
        commentContent: '',
        commentLikeCount: 11,
        commentTime: '22:17',
        portraitImg: '/static/head.png'
      }
    }
  }
</script>

<style scoped>
  .container-fluid {
    padding-top: 5px;
    padding-bottom: 5px;
  }
  span {
    white-space: nowrap;
  }
  .right {
    border-bottom: solid;
    border-width: 1px;
    border-color: #eeeeee;
    padding-bottom: 5px;
  }
  p {
    margin-bottom: 5px;
  }
</style>



